.navbar {
    display: flex;
    justify-content: space-between;
    box-sizing: border-box;
    border-bottom: 1px solid #e5e5e5;
    background-color: #fff;
    height: 100%;
}

.left {
    display: flex;
    align-items: center;
}

.logo {
    display: flex;
    align-items: center;
    width: 200px;
    padding-left: 20px;
    box-sizing: border-box;
}

.logo-name {
    color: var(--color-text-1);
    font-weight: 500;
    font-size: 20px;
    margin-left: 10px;
    font-family: 'PingFang SC';
}

.right {
    display: flex;
    list-style: none;
    padding-right: 20px;

    li {
        padding: 0 8px;
        display: flex;
        align-items: center;
    }

    a {
        text-decoration: none;
        color: #293344;
    }
}